/* eslint-disable */

import React, { Component } from 'react';
import './loading.css';
import { Redirect } from 'react-router';
import ReactTimeout from 'react-timeout';

class Loading extends Component {
  constructor(props) {
    super(props);
    this.redirect = this.redirect.bind(this);
  }
  redirect() {
    const url = location.origin + '/result';
    location.href = url;
  }
  componentDidMount() {
    const storage = window.localStorage;
    storage.setItem('name', this.props.name);
    storage.setItem('loveState', this.props.loveState);
    setTimeout(this.redirect.bind(this), 3000);
  }
  render() {

    return (
      <div className="loading">
        <p className="loading-text"><span class="blind">   </span></p>
        <div className="loading-dots-group">
          <div className="loading-dots loading-dots-1"></div>
          <div className="loading-dots loading-dots-2"></div>
          <div className="loading-dots loading-dots-3"></div>
        </div>
      </div>
    )
  }
}

export default Loading


