import React, { Component } from 'react';
import './name.css';

class NameView extends Component {
  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
    this.handleName = this.handleName.bind(this);
    this.state = { 
      name: ''
    }
  }
  handleName(event) {
    this.setState({name: event.target.value});
  }
  onSubmit(event) {
    event.preventDefault();
    this.props.setName(this.state.name);
  }
  render() {
    return (
      <div className="name-form">
        <form onSubmit={this.onSubmit}>
          <label htmlFor="name"><span className="blind"> </span></label>
          <input type="text" id="name" className="inp-text" value={this.state.name} onChange={this.handleName} />
          <button type="submit" className="btn-next"><span class="blind"></span></button>
        </form>
      </div>
    )
  }
}

export default NameView;


