import React, { Component } from 'react';
import './entry.css';

class EntryView extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className='entry'>
        <button type="button" className="btn btn-start" onClick={this.props.onLoveStart}><span className="blind"> </span></button>
        <button type="button" className="btn btn-timing" onClick={this.props.onLoveTiming}><span className="blind"> </span></button>
        <button type="button" className="btn btn-end" onClick={this.props.onLoveEnd}><span className="blind"> </span></button>
      </div>
    )
  }
}

export default EntryView


