import React, { Component } from 'react';
import '../node_modules/noto-sans-kr/styles.css';
import './main.css';
import Entry from './entry';
import Name from './name';
import Loading from './loading';

class MainApp extends Component {
  constructor(props) {
    super(props);
    this.handleLoveStart = this.handleLoveStart.bind(this);
    this.handleLoveTiming = this.handleLoveTiming.bind(this);
    this.handleLoveEnd = this.handleLoveEnd.bind(this);
    this.setName = this.setName.bind(this);
    this.state = {
      loveState: '',
      name: ''
    };
  }
  handleLoveStart() {
    this.setState({loveState: 'start'});
  }
  handleLoveTiming() {
    this.setState({loveState: 'timing'});
  }
  handleLoveEnd() {
    this.setState({loveState: 'end'});
  }
  setName(inpName) {
    this.setState({name: inpName});
  }
  render() {
    const { loveState, name } = this.state;

    var entryView = null;

    if(loveState === '') {
      entryView = <Entry onLoveStart={this.handleLoveStart} onLoveTiming={this.handleLoveTiming} onLoveEnd={this.handleLoveEnd} />
    } else {
      entryView = <Name loveState={this.loveState} name={this.name} setName={this.setName} />
    }

    var contentsView = null;

    if(name === '') {
      contentsView = (
        <div className="contents">
          <p className="desc"><span className="blind">       .</span></p>
          {entryView}
        </div>
      )
    } else {
      contentsView = (
        <div className="contents">
          <Loading loveState={loveState} name={name} />
        </div>
      )
    }
    
    return (
      <div className="container">
        <header>
          <h1 className="title"></h1>
        </header>
        {contentsView}
        <div className="tree-1"></div>
        <div className="tree-2"></div>
      </div>
    );
  }
}

export default MainApp;



