import React, { Component } from 'react';
import './result.css';

let text = `     . 
           . 
          ? 
,    .
 ,  ?`

class Result extends Component {
  constructor(props) {
    super(props);
    const storage = window.localStorage;
    this.state = {
      name: storage.getItem('name'),
      loveState: storage.getItem('loveState')
    }
  }
  render() {
    const loveState = this.state.loveState;
    const resultClass = 'result-text ' + 'state-' + loveState;
    return (
      <div className="container">
        <header>
          <h1 className="title"></h1>
        </header>
        <div className="result">
          <p className={resultClass}><strong>{this.state.name}</strong></p>
          <div className="result-textarea">{text}</div>
        </div>
        <div className="tree-1"></div>
        <div className="tree-2"></div>
      </div>
    )
  }
}

export default Result;


